.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hand {
  transform-origin: bottom;
  animation-name: move;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
}

.stopwatch__hand--second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.stopwatch__hand--speed-up-minute {
  animation-duration: 600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
}

.stopwatch__hand--speed-up-second {
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes move {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}
